import axios from 'axios' // axios
import url from '../../../public/config' // axios
import NProgress from "nprogress";
import { Notification,Message, MessageBox, Loading } from 'element-ui'

const apiUrl = url.config.apiUrl;

const instance = axios.create({
  baseURL: apiUrl, //
  headers: {
  },
  transformResponse: [function (data) {}],
});

// http request 
instance.interceptors.request.use(
config => {
  NProgress.start()
  return config
},
err => {
  return Promise.reject(err)
}
)
// response 
let MessageBoxshow = true;
instance.interceptors.response.use(
response => {
  NProgress.done()
  let data;

  if(response.data === undefined){
    try {
     data =JSON.parse(response.request.responseText)//JSON,
    } catch(e) {
      data =response.request.responseText
    }
  }
  else{
    data = response.data;
  }
  if (data.errorcode == '10002' || data.errorcode == '10004') {
    if(MessageBoxshow){
      MessageBox.alert(data.msg, '', { //MessageBoxshowlow bug
        confirmButtonText: '',
        callback: () => {
         
        }
      });
      MessageBoxshow=false;
    }
  }else {
    MessageBoxshow=true;
    return data
  }

},
error => {
  return Promise.reject(error)
})

export default function $axios (options) {
  return new Promise((resolve, reject) => {
    
    //
    instance(options)
      .then((res) => {
        resolve(res);
        return false
      })
      .catch((error) => {
        reject(error)
        //Notification.warning('!');
      })
  })
}
